import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { PlaceAdSectionPage } from './place-ad-section.page';
import { RouteDataType } from '@core/services/route.service';

const routes: Routes = [
	{
		path: '',
		component: PlaceAdSectionPage,

		data: {
			[RouteDataType.IS_HEADER_VISIBLE]: false,
			[RouteDataType.IS_FOOTER_VISIBLE]: false,
		},
	},
];

@NgModule({
	imports: [RouterModule.forChild(routes)],
	exports: [RouterModule],
})
export class PlaceAdSectionRoutingModule {}
